// List
//
// Your standard button suitable for clicking.
//
//
// Markup: list/list.twig
//
// Style guide: Pattern Library.list

// list
.list {
  margin: 0;
  padding: 0;

  .list__item {
    position: relative;
    margin: 0;
    border-bottom: $base-border;
    list-style-type: none;
    list-style-image: none;
    padding: 0;

    &:first-child {
      a {
        padding-top: 0;     
      }
    }

    &:last-child {
      border-bottom: none;
    }

    a {
      display: block;
      background: none;
      padding: $base-spacing;

      &:hover {
        text-decoration: none;
      }

      .label {
        font-size: $h3-font-size;
        font-weight: $h3-font-weight;
        line-height: 1;
      }
    }
  }

  .panel & {
    li {
      a {
        padding-left: 0;
        padding-right: 0;
      }

      &:last-child {
        a {
          padding-bottom: 0;        
        }
      }
    }
  }
}